@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');
// @import "base";
@import url('https://cdn.jsdelivr.net/gh/jesus1554/Sass-basis@1.1/base.css');
@import "vars";

body {
    font-family: 'Open Sans', sans-serif;
}

.btn {
    cursor: pointer;
    border-radius: 2rem;
    padding: 1rem 2rem;
    color: #fff;
    text-decoration: none;
    font-weight: 700;
    text-transform: capitalize;
    transition: all .5s ease-out;
}
.btn-a {
    background: rgb(228, 87, 46);
    background: linear-gradient(145deg, $naranja 0%, $amarillo 100%);
}

.btn-b {
    @include button-color($azul, true)
}

.btn-c {
    @include button-color($verde-claro, true)
}

.site-header {
    @include center(true);

    h1 {
        font-size: 6rem;
        color: $azul;
    }

    height: 100vh;
    position: relative;
    overflow: hidden;
    width: 100%;
}

.h-100vh {
    // @include center(true);
    text-align: center;
    margin-top: 1.5rem;
}

.site-header:before {
    content: '';
    position: absolute;
    bottom: 0;
    width: 100vw;
    background-image: url("/img/wave-bottom.svg");
    height: 22rem;
}

.flecha {
    color: $azul;
    font-size: 3rem;
}

.site-navigation {
    position: sticky;
    top: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #dadce4;
    height: 12vh;
    background-color: #fff;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, .12), inset 0 -1px 0 0 #dadce0;
}

.logo {
    img {
        height: 6rem;
    }

    padding: 1rem 2rem;

}

.links {
    margin: 1rem 4rem 1.2rem 2rem;

    a {
        color: #000;
        text-decoration: none;
        margin: 1rem 2rem;
        padding: 2.4rem 1rem;
    }
}

.underline {
    border-bottom: 3px solid $azul;
}

#nosotros {
    height: 100vh;
    @include center(true);
    text-align: center;
}

#servicios {
    height: 100vh;
    @include center(true);
    text-align: center;
    color: #fff;
    background-color: $verde-fuerte;
}
